.banner2-body {
    background-image: url("assets/background.png");
    background-color: #ffffff;
}

.banner2-top {
    width: 120rem;
    height: 12.563rem;
    background-image: linear-gradient(225deg,
		                      #c2e1de 0%,
		                      #aad3c7 35%,
		                      #8ec8ba 65%,
		                      #88ac9e 100%);
}

.banner2-container {
    min-height: calc(100vh - 12.563rem - 27.875rem);
}

.page-footer {
    width: 120rem;
    height: 27.875rem;
}

.icon-open-menu {
    width: 2.875rem;
    height: 2.563rem;
}

.logo-white {
    width: 17.25rem;
    height: 9.438rem;
}

.text-link {
    height: 2.25rem;
    font-family: Myriad Pro;
    font-size: 2rem;
    font-weight: bold;
    font-stretch: normal;
    line-height: 1.875rem;
    letter-spacing: 0rem;
    color: #ffffff;
}

.text-title {
    width: 8rem;
    height: 2rem;
    font-family: Myriad Pro;
    font-size: 1.563rem;
    font-weight: "Black";
    font-stretch: normal;
    line-height: 1.875rem;
    letter-spacing: 0rem;
    color: #000000;
}

.text-option {
    width: 8rem;
    height: 9.375rem;
    font-family: Myriad Pro;
    font-size: 1rem;
    font-weight: normal;
    font-stretch: normal;
    line-height: 1.375rem;
    letter-spacing: 0rem;
    color: #000000;
}
